

























import Vue from 'vue';
import Component from 'vue-class-component';
import {Prop} from 'vue-property-decorator';

@Component({
  name: 'CodeWrapper'
})
export default class CodeWrapper extends Vue {
  @Prop(String)
  public code: string;
  @Prop(String)
  public md: string;
  public showCode = false;


  public toggleCode() {
    return this.showCode = !this.showCode;
  }

}
